.template-home {
   height: 100vh;
   padding: 2rem 5.69rem;

   .title-container {
      align-items: center;
      display: flex;
      justify-content: space-between;
      margin-right: 6rem;

      .button-anchor {
         background-image: url('/assets/img/button_all_options.svg');
         background-size: cover;
         display: inline-block;
         height: 2.375rem;
         width: 13.5625rem;
      }

      .button-anchor:hover {
         background-image: url('/assets/img/button_all_options_hover.svg');
         transition: 1s ease;
      }

      .title {
         max-width: 16.5em;

         h3 {
            -webkit-text-stroke: 0.7px black;
            color: transparent;
            font-size: $size-h3;
            font-style: normal;
            font-weight: $sbold;
            letter-spacing: -0.18rem;
            line-height: 95%;
         }
      }
   }

   .grid-container {
      display: grid;
      grid-gap: 0.88rem;
      grid-template-columns: 1fr 1fr 1fr;
      padding: 1.4em 5.8em 4em 5.8em;
   }
}

.marquee-container {
   overflow: hidden;
   position: relative;
   background-color: $black;
   width: 100%;

   .marquee-content {
      width: 100%;
      display: flex;
      gap: 2rem;
      white-space: nowrap;
      animation: marquee 10s linear infinite;
      padding: 0.7rem;
      font-size: 1.2em;
      font-style: normal;
      font-weight: 500;
      line-height: 100%;
      letter-spacing: -0.09rem;

      &__white {
         color: $white;
      }
      &__yellow {
         color: $yellow;
      }
   }
}

@keyframes marquee {
   0% {
      transform: translateX(0);
   }
   100% {
      transform: translateX(-100%);
   }
}
