.header {
   background-color: $yellow;
   display: flex;
   flex-direction: column;
   height: 100vh;
   justify-content: space-between;
   padding: 2.88rem 2rem 3.06rem 5.69rem;

   &__nav {
      align-items: center;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;

      div {
         align-items: center;
         display: flex;
         gap: 0.5rem;

         a {
            background-color: $white;
            border-radius: $br-small;
            font-weight: $bold;
            padding: 0.5em 1em;

            &:hover {
               background-color: $black;
               color: $white;
               transition: 0.5s ease-out;
            }
         }

         a:last-child {
            background-color: $black;
            color: $white;
            border-radius: $br-small;
            font-weight: $bold;
            padding: 0.5em 1em;
         }
      }
   }

   &__container {
      font-size: $size-h2;
      font-weight: $bold;

      h1 {
         color: #fff;
         font-size: $size-h1;
         font-weight: 600;
         letter-spacing: -0.5625rem;
         line-height: 90%;
      }

      h3 {
         color: $black;
         font-size: $size-h2;
         font-weight: $medium;
         letter-spacing: -0.325rem;
         line-height: 90%;
         margin-top: 2.5rem;
      }

      .logo {
         height: auto;
         margin-left: 2.7em;
         margin-top: 0.2em;
         position: absolute;
         width: 35%;
      }

      .lamp {
         height: 500px;
         position: absolute;
         right: 0;
         top: 125px;
         width: auto;

         &:hover {
            box-shadow: -25px 5px 100px white;
         }
      }
   }
}
