.grid-feeds {
   display: grid;
   grid-gap: 0.88rem;
   grid-template-columns: 1fr 1fr 1fr;
   padding: 1.4em 5.8em 4em 5.8em;

   // Ajuste para la cuarta imagen
   .feed-container:nth-child(4) {
      grid-column: 3;
   }

   // Ajuste para el texto de la imagen
   .image-text {
      // AquÃ­ puedes estilizar tu texto
      border-radius: $br-normal;
      border: 1px solid $black;
      grid-column: span 2;
      padding: 1.94rem 5.69rem 1.94rem 2.31rem;

      p {
         font-size: 1.125rem;
         line-height: 95%;
         letter-spacing: -0.0675rem;
      }

      h3 {
         font-size: 2.175rem;
         font-weight: $extrabold;
         line-height: 95%;
         letter-spacing: -0.1425rem;
         margin-top: 1rem;
      }

      h4 {
         color: $yellow;
         font-size: $f-md;
         line-height: 95%;
         letter-spacing: -0.12rem;
         margin: 1rem 13.56rem 0rem 0rem;
      }
   }
}
